import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Redirect } from 'react-router-dom';

import { Container, Row, Col, Card, CardBody, Label, FormGroup, Button, Alert } from 'reactstrap';
import classNames from 'classnames';
import { AvForm, AvGroup, AvInput, AvFeedback } from 'availity-reactstrap-validation';

import { loginUser } from '../../redux/actions';
import { isUserAuthenticated } from '../../helpers/authUtils';
import LoaderWidget from '../../components/Loader';
import ebrsLogo from '../../assets/images/research.svg';

class Login extends Component {
    _isMounted = false;

    constructor(props) {
        super(props);

        this.handleValidSubmit = this.handleValidSubmit.bind(this);
        this.state = {
            username: 'admin',
            password: 'admin',
        };
    }

    componentDidMount() {
        this._isMounted = true;
    }

    componentWillUnmount() {
        this._isMounted = false;
    }

    /**
     * Handles the submit
     */
    handleValidSubmit = (event, values) => {
        console.log('Login.js');
        this.props.loginUser(values.username, values.password, this.props.history);
    };

    /**
     * Redirect to root
     */
    renderRedirectToRoot = () => {
        const isAuthTokenValid = isUserAuthenticated();
        if (isAuthTokenValid) {
            return <Redirect to="/" />;
        }
    };

    render() {
        const isAuthTokenValid = isUserAuthenticated();

        //const onDismiss = () => 

        return (
            <React.Fragment>
                {this.renderRedirectToRoot()}

                {(this._isMounted || !isAuthTokenValid) && (
                    <div className="account-pages mt-5 mb-5">
                        <Container>
                            <Row className="justify-content-center">
                                <Col lg={6}>
                                    <Card>
                                        <div className="card-header pt-4 pb-4 text-center auth-header">
                                            <span className="logo-lg">
                                                <img src={ebrsLogo} alt="logo" height="32" className="logo-img"/> e-BRS / ACR
                                            </span>
                                        </div>

                                        <CardBody className="p-4 position-relative">
                                            {/* preloader */}
                                            {this.props.loading && <LoaderWidget />}

                                            <div className="text-center w-75 m-auto">
                                                <h4 className="text-dark-50 text-center mt-0 font-weight-bold">
                                                    Autenticación
                                                </h4>
                                                <p className="text-muted mb-4">
                                                    Ingrese su nombre de usuario y contraseña.
                                                </p>
                                            </div>

                                            {this.props.error && (
                                                <Alert color="danger" isOpen={this.props.error ? true : false}>
                                                    <div><i className={classNames('dripicons-wrong', 'mr-2')}></i> {this.props.error}</div>
                                                </Alert>
                                            )}

                                            <AvForm onValidSubmit={this.handleValidSubmit}>
                                                <AvGroup>
                                                    <Label for="username">Usuario</Label>
                                                    <AvInput
                                                        type="text"
                                                        name="username"
                                                        id="username"
                                                        placeholder="Nombre de Usuario"
                                                        value={this.state.username}
                                                        required
                                                    />
                                                    <AvFeedback>Debe especificar un nombre de usuario</AvFeedback>
                                                </AvGroup>

                                                <AvGroup>
                                                    <Label for="password">Contraseña</Label>
                                                    <a
                                                        href="/account/forget-password"
                                                        className="text-muted float-right">
                                                        <small>Recuperar contraseña</small>
                                                    </a>
                                                    <AvInput
                                                        type="password"
                                                        name="password"
                                                        id="password"
                                                        placeholder="Contraseña"
                                                        value={this.state.password}
                                                        required
                                                    />
                                                    <AvFeedback>Debe especificar su contraseña</AvFeedback>
                                                </AvGroup>

                                                <FormGroup>
                                                    <Button color="success" className="float-right">Ingresar</Button>
                                                </FormGroup>

                                            </AvForm>
                                        </CardBody>
                                    </Card>
                                </Col>
                            </Row>

                            {/* <Row className="mt-1">
                                <Col className="col-12 text-center">
                                    <p className="text-muted">
                                        Don't have an account?{' '}
                                        <Link to="/account/register" className="text-muted ml-1">
                                            <b>Register</b>
                                        </Link>
                                    </p>
                                </Col>
                            </Row> */}
                        </Container>
                    </div>
                )}
            </React.Fragment>
        );
    }
}

const mapStateToProps = state => {
    const { user, loading, error } = state.Auth;
    return { user, loading, error };
};

export default connect(
    mapStateToProps,
    { loginUser }
)(Login);
